@import url("https://fonts.googleapis.com/css?family=Poppins");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  list-style-type: none;
  text-decoration: none;
  outline: none;
  resize: none;
  text-transform: capitalize;
  background-repeat: no-repeat;
}

html {
  scroll-behavior: smooth;
}

/* Navbar.css */

.navbar {
  background-color: #333;
  padding: 1rem;
  position: relative;
  bottom: 0;
  z-index: 999;
  height: 75px;
  position: sticky;
  box-shadow: inset 0 -1px 0 0 #4f46e5;
  width: 100%;
  top: 0%;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-logo {
  color: #fff;
  text-decoration: none;
  font-size: 1.5rem;
}

.nav-menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav-item {
  margin-right: 1rem;
}

.nav-link {
  text-decoration: none;
  color: #fff;
  font-size: 1rem;
}

.menu-icon {
  display: none;
  cursor: pointer;
  color: #fff;
  font-size: 1.5rem;
}

@media screen and (max-width: 768px) {
  .menu-icon {
    display: block;
  }

  .nav-menu {
    flex-direction: column;
    position: absolute;
    top: 80px;
    left: 0;
    width: 100%;
    background-color: #333;
    text-align: center;
    display: none;
  }

  .nav-menu.active {
    display: flex;
  }

  .nav-item {
    margin: 0;
    margin-bottom: 1rem;
  }
}

