body footer .grid-container .contacts form input {
  height: 35px;
  box-sizing: border-box;
  padding: 2%;
  box-shadow: none;
  border-color: transparent;
  border-radius: 5px;
}

body footer .grid-container .contacts .grid-3 i,
body footer .grid-container .contacts .grid-container i {
  color: #fff;
  font-size: 13px;
  background-color: #000000;
  width: 40px;
  height: 40px;
  padding: 10px;
  border-radius: 50%;
}

@media screen and (max-width: 768px) {
  body {
    overflow-x: hidden !important;
  }

  body header {
    width: 100%;
    padding-bottom: 10px;
  }

  body header .title {
    width: 50%;
  }

  body header .title p {
    font-size: 15px;
  }

  body header ul {
    left: 0%;
    display: none;
  }

  body main {
    animation: none;
    height: 100vh;
    overflow-x: hidden;
  }

  body main .overlay p {
    width: 80%;
    font-size: 25px;
    text-align: center;
  }

  body .launches {
    display: inline-flex;
    flex-wrap: wrap-reverse;
  }

  body section {
    height: max-content;
    padding: 5%;
  }

  body section .invest * {
    text-align: center;
  }

  body section .invest * a {
    width: 80%;
    margin: auto;
  }

  body section .about-container {
    padding: 5%;
    text-align: center;
  }

  body section .about-container .text-container small {
    width: 100%;
  }

  body section .about-container img {
    display: none;
  }

  body section .container {
    width: 90%;
  }

  body section .container .img-container {
    width: 100%;
    text-align: center;
  }

  body section .container .img-container a {
    width: 80%;
    margin: auto;
  }

  body section .container .text-container {
    text-align: center;
  }

  body section .container .text-container a {
    width: 80%;
    margin: auto;
  }

  body section .grid-2,
  body section .grid-2 .properties-container,
  body section .grid-3,
  body section footer .grid-container,
  body footer section .grid-container {
    grid-template-columns: repeat(1, 1fr);
    width: 100%;
  }

  body section .grid-2 *,
  body section .grid-2 .properties-container *,
  body section .grid-3 *,
  body section footer .grid-container *,
  body footer section .grid-container * {
    margin-bottom: 10px;
  }

  body section .grid-2 * a,
  body section .grid-2 .properties-container * a,
  body section .grid-3 * a,
  body section footer .grid-container * a,
  body footer section .grid-container * a {
    text-align: center;
  }

  body footer {
    height: max-content;
    padding-top: 5%;
  }

  body footer .grid-container .intro {
    box-sizing: border-box;
    padding-left: 5%;
    align-items: flex-start;
  }
}
